import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { Button } from 'carbon-components-react';
import "carbon-components/scss/globals/scss/styles.scss";

const ComponentDef = function(props, ref) {
    return view(props, ref);
}

const view = (props, ref) => {
    return (<div ref={ref}> 
        <div>Home Page</div>
        <Button kind='danger' size='sm' onClick={() => {
            props.dispatchEvent("HOME#clicked", { message: 'Home button was clicked' });
        }}>Click me</Button>
        <div>{props.message}</div>
    </div>);
}

const styles = `
    div {
        background-color: white;
    }
`

const props = {
    "message": "Hello"
}

const WrappedComponent = wrapper(React.forwardRef(ComponentDef));
export {WrappedComponent, styles, props};